import React from 'react'
import uniq from 'lodash/uniq'
import { IoIosLock } from 'react-icons/io';
import { Link } from 'react-router-dom';
import styled from 'styled-components';

import csvData from '../data.csv'
import useCsv from '../useCsv'
import { icons } from '../photo'

import Box from '../../components/Box';
import Flex from '../../components/Flex';
import Image from '../../components/Image';

const StyledLink = styled(Link)`
  text-decoration: none;
  color: black;
`

const MountainList = () => {
  const { data } = useCsv(csvData)
  const region = uniq(data.map(({ location }) => location))
  const useData = data.reduce((res, { location, src, isLock, name }) => {
    res[location].push({ name, src, isLock})
    return res
  }, { 北北基: [], 澎金馬: [], 宜花東:[], 桃竹苗: [], 中彰投: [], 雲嘉南: [], 高屏: []})
  return (
    <Flex position="absolute" top="4em" left="0" right="0" bg="white"px="1em" py="1em" zIndex={99}>
      {region.map((r, i, { length }) => (
        <Box width={1 / length} key={i}>
          <Box
            mx="0.5rem"
            fontSize="1.25em"
            fontWeight="bold"
            borderBottom="1px solid"
            pb="0.625rem"
            lineHeight="1"
          >
            {r}
          </Box>
          {useData[r].map(({ name, src, isLock }, k) => (
            <Flex as={StyledLink} to={`/topics/${src}`} target="_blank" pointerEvents={isLock && 'none'} mt="1.125em" alignItems="center" key={k}>
              <Box position="relative" borderRadius="50%" width="2.5em" bg="white" overflow="hidden">
                <Image src={icons[src]} />
                {isLock && (
                  <Box
                    position="absolute"
                    left="0"
                    right="0"
                    top="0"
                    bottom="0"
                    bg="white"
                    opacity="0.5"
                    borderRadius="1em"
                  >
                    <Box position="absolute" left="50%" top="50%" transform="translate(-50%, -50%)">
                      <IoIosLock size="1.25em" />
                    </Box>
                  </Box>
                )}
              </Box>
              <Box ml="0.5em">{name}</Box>
            </Flex>
          ))}
        </Box>
      ))}
    </Flex>
  )
}

export default MountainList
